"use client"

import { useState, useRef, useEffect } from "react"
import { Search, Bell, Maximize2, LogOut, User } from "lucide-react"

interface DashboardHeaderProps {
  userName: string
  userRole: string
  onLogout: () => void
}

export default function DashboardHeader({ userName, userRole, onLogout }: DashboardHeaderProps) {
  const [openMenu, setOpenMenu] = useState(false)
  const menuRef = useRef<HTMLDivElement>(null)

  const toggleFullscreen = () => {
    if (document.fullscreenElement) document.exitFullscreen()
    else document.documentElement.requestFullscreen()
  }

  // Close dropdown on outside click
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
        setOpenMenu(false)
      }
    }
    document.addEventListener("mousedown", handleClickOutside)
    return () => document.removeEventListener("mousedown", handleClickOutside)
  }, [])

  return (
    <div className="bg-[#f8f4f3] border-b border-gray-200 px-6 py-2 flex items-center shadow-md shadow-black/5 sticky top-0 left-0 z-30">
      <h1 className="text-lg font-semibold text-gray-900">Dashboard</h1>

      <div className="flex items-center gap-4 ml-auto relative" ref={menuRef}>
        {/* Search Icon */}
        <button className="text-gray-400 hover:text-gray-600 w-8 h-8 flex items-center justify-center rounded">
          <Search size={20} />
        </button>

        {/* Notification Bell */}
        <button className="text-gray-400 hover:text-gray-600 w-8 h-8 flex items-center justify-center rounded relative">
          <Bell size={20} />
          <span className="absolute top-0 right-0 w-3 h-3 bg-lime-400 border-2 border-white rounded-full"></span>
        </button>

        {/* Fullscreen Toggle */}
        <button onClick={toggleFullscreen} className="text-gray-400 hover:text-gray-600 w-8 h-8 flex items-center justify-center rounded">
          <Maximize2 size={20} />
        </button>

        {/* User Profile Button */}
        <button
          className="flex items-center gap-2 pl-4 border-l border-gray-200"
          onClick={() => setOpenMenu((prev) => !prev)}
        >
          <div className="flex-shrink-0 w-10 h-10 relative">
            <div className="p-1 bg-white rounded-full">
              <div className="w-8 h-8 bg-green-400 rounded-full flex items-center justify-center">
                <span className="text-sm font-bold text-white">
                  {userName?.slice(0, 2).toUpperCase()}
                </span>
              </div>
              <div className="top-0 left-7 absolute w-3 h-3 bg-lime-400 border-2 border-white rounded-full animate-ping"></div>
              <div className="top-0 left-7 absolute w-3 h-3 bg-lime-500 border-2 border-white rounded-full"></div>
            </div>
          </div>
          <div className="hidden md:block">
            <h2 className="text-sm font-semibold text-gray-800">{userName}</h2>
            <p className="text-xs text-gray-500">{userRole}</p>
          </div>
        </button>

        {/* Dropdown Menu */}
        {openMenu && (
          <div className="absolute right-0 top-14 bg-white shadow-xl rounded-md w-52 border border-gray-200 py-2 z-50 animate-fade-in">
            <button className="w-full px-4 py-2 flex items-center gap-2 text-gray-700 hover:bg-gray-100">
              <User size={16} />
              Profile
            </button>
            <button
              onClick={onLogout}
              className="w-full px-4 py-2 flex items-center gap-2 text-red-600 hover:bg-red-100"
            >
              <LogOut size={16} />
              Logout
            </button>
          </div>
        )}
      </div>
    </div>
  )
}
